import * as p from "parjs";
import { many, map, mapConst, or, stringify } from "parjs/combinators";
import { called } from "../parseUtils";

export const tokenFactory = {
  firstLevelDefinition: (content, heading) => ({
    type: "firstLevelDefinition",
    content: content,
    heading: heading,
  }),
  secondLevelDefinition: (i, content, heading) => ({
    type: "secondLevelDefinition",
    number: i,
    content: content,
    heading: heading,
  }),
  thirdLevelDefinition: (content, heading) => ({
    type: "thirdLevelDefinition",
    content: content,
    heading: heading,
  }),
  fourthLevelDefinition: (content, heading) => ({
    type: "fourthLevelDefinition",
    content: content,
    heading: heading,
  }),
  exampleSentenceGroup: (sentences) => ({
    type: "exampleSentenceGroup",
    content: sentences,
  }),
  synonymSection: (content) => ({ type: "synonymSection", content: content }),
  cliticSection: (heading, content) => ({
    type: "cliticSection",
    heading: heading,
    content: content,
  }),
  literalQuote: (content, innerQuote) => ({
    type: "literalQuote",
    content,
    innerQuote,
  }),
  exampleSentence: (content, innerQuote) => ({
    content,
    innerQuote,
    type: "exampleSentence",
  }),
  metaInformation: (content) => ({ type: "metaInformation", content: content }),
};

export const linebreak = p.newline().pipe(mapConst({ type: "linebreak" }));

const numbers =
  "";

export const whiteCircledNumber = () => {
  const conversions = numbers.split("").reduce((result, char, i) => {
    result[char] = i;
    return result;
  }, {});

  return p.anyCharOf(numbers).pipe(
    map((parsedChar) => {
      return { number: conversions[parsedChar], char: parsedChar };
    }),
    called("whiteCircledNumber")
  );
};

export const fullWidthNumber = () => {
  const num0 = p.string("").pipe(mapConst("0"));
  const num1 = p.string("").pipe(mapConst("1"));
  const num2 = p.string("").pipe(mapConst("2"));
  const num3 = p.string("").pipe(mapConst("3"));
  const num4 = p.string("").pipe(mapConst("4"));
  const num5 = p.string("").pipe(mapConst("5"));
  const num6 = p.string("").pipe(mapConst("6"));
  const num7 = p.string("").pipe(mapConst("7"));
  const num8 = p.string("").pipe(mapConst("8"));
  const num9 = p.string("").pipe(mapConst("9"));

  return num0.pipe(
    or(num1, num2, num3, num4, num5, num6, num7, num8, num9),
    many(),
    stringify(),
    map(parseInt),
    called("fullWidthNumber")
  );
};

